
import {Component, Vue, Watch} from "vue-property-decorator";
import ActionBlock from '@/components/search/actionBlock.vue';
import moment from "moment";
import Service from "@/https/gpu/list";
import GpuEdit from "@/views/gpu/gpuEdit.vue";
import RightClick from "@/views/vmOp2/component/right-click.vue";
import {rightClick} from "@/utils/vmOp2/rightClick";
import {hideMenu} from "@/utils/vmOp2/hideMenu";
import { findPodIdByHostId } from "@/utils/vmOp2/findPodId"
import Sale from "@/views/vmOp2/cluster/host/sale.vue";
@Component({
  components:{
    Sale,
    RightClick,
    GpuEdit,
    ActionBlock
  }})
export default class list extends Vue {
  private gpu_list:Array<Object> = [];
  private search_reqData ={}
  private visible:boolean = false
  private oper_info = {}
  private err = 'err'
  private normal = 'normal'
  private operate_auth = []
  private search_status=[]
  private multi_rows:any=[];
  private hand_type = ''
  private visible_sale:boolean = false
  private status_list =[
    {text: '正常', value: 'READY'},
    {text: 'FF', value: 'DISCONNECTED'},
  ]
  private menus= [
    {label: '编辑',value: 'edit',single:true,disabled:false},
    {label: '禁售',value: 'forbid',single:true,disabled:false},
    {label: '取消禁售',value: 'cancel_forbid',single:true,disabled:false},
    {label: '同步',value: 'sync',single:true,disabled:false},
  ]
  private page_info = {
    page_sizes: [20, 50, 100],
    page_size: 20,
    page_index: 1,
    total: 0
  };
  private search_con = {
    // host_id: { placeholder: "请输入物理机ID" },
    vm_id: { placeholder: "请输入云服务器ID"},
    card_name: { placeholder: "请输入显卡名称"},
    host_ip:{placeholder:'请输入管理网IP'},
    //host_name:{placeholder:'请输入主机名称'},
    customer_id: {placeholder:'请输入客户ID'},
    customer_name: {placeholder:'请输入客户名称'},
    sn: { placeholder: "请输入sn号" },
    create_time: {
      placeholder: ["开始时间", "结束时间"],
      type: "daterange",
      width: "360",
      clearable: true,
      dis_day: 1,
      defaultTime: []
    },
  }
  @Watch('visible')
  private watch_visible(nv){
    if(!nv){
      this.oper_info={};
      this.FnGetList()
    }
  }
  @Watch("$store.state.pod_id")
  private watch_pod(nv){
    if(!nv){
      return;
    }
    this.FnGetList()
  }
  @Watch('multi_rows',{immediate:true,deep:true})
  private watch_multi(){
    this.handleMenus()
  }
  private hasStatus(status: string) {
    return this.multi_rows.every(item => item.status === status);
  }
  private menuConditions: { [key: string]: () => boolean } = {
    'cancel_forbid': () => !this.hasStatus('禁售'),
    'forbid': () => this.hasStatus('禁售')
  }
  //
  private handleMenus() {
    for (const item of this.menus) {
      item.disabled = this.menuConditions[item.value] ? this.menuConditions[item.value]() : false;
    }
  }
  private infoClick(item) {
    if (item.disabled) {
      return; // 禁用元素时不执行点击事件
    }
    const {label, value}=item
    if(value === 'edit'){
      this.FnEdit(this.multi_rows[0])
    }else if(value === 'forbid'){
      this.FnHandleGpuSale(this.multi_rows[0],'forbid')
    }else if(value === 'cancel_forbid'){
      this.FnHandleGpuSale(this.multi_rows[0],'cancel_forbid')
    }else if(value === 'sync'){
      this.FnSync(this.multi_rows[0])
    }
    hideMenu()
  }
  //同步GPU底层状态
  private async FnSync(row){
    let pod_id = findPodIdByHostId(this.$route.params.id)
    let res = await Service.update_host_gpu_status({
      pod_id:pod_id,
      host_id:row.host_id,
      pci_address:row.pci_address
    })
    if(res.code === 'Success'){
      this.$message.success(res.message)
    }
  }
  //GPU禁售
  private async FnHandleGpuSale(row,type){
    this.visible_sale = true
    this.oper_info = [row]
    this.hand_type = type
  }
  private handleFilterChange(val){
    this.search_status = val.status;
    console.log('this.search_status',this.search_status)
    this.FnGetList();
  }
  private handleSelectionChange(data){
    this.multi_rows = data
  }
  //改变点击行得选中状态
  private FnOperRow(row){
    (this.$refs.table as any).toggleRowSelection(row)
  }
  //改变选中行的背景颜色
  rowStyle({row}) {
    const isSelected = this.multi_rows.some(item => item.index === row.index);
    if (isSelected) {
      return 'rowStyle'
    }
  }
  //右键弹出操作
  FnRightClick(row,column,event){
    //判断当前行是否被选中，没选中时需选中并弹出菜单
    console.log(event)
    const isSelected = this.multi_rows.some(item => item.index === row.index);
    if (!isSelected) {
      (this.$refs.table as any).toggleRowSelection(row)
    }
    rightClick(row,column,event)
  }
  created(){
    this.FnGetList();
    this.operate_auth = this.$store.state.auth_info[this.$route.name];
    //监听点击事件，点击时隐藏右键菜单
   // document.addEventListener('click', hideMenu);
  }
  private async FnEdit(row){
    this.visible = true
    this.oper_info = row
  }
  private FnGoToMonitor(row) {
    this.$router.push({name:'host_monitor',params:{id:row.host_id,gpuTab:'gpu'}})
  }
  private FnSearch(data: any = {}) {
    this.search_reqData = {
      vm_id: data.vm_id,
      card_name: data.card_name,
      customer_id:data.customer_id,
      customer_name:data.customer_name,
      host_ip:data.host_ip,
      host_name:data.host_name,
      sn:data.sn,
      create_time_start:data.create_time && data.create_time[0]
        ?  moment(data.create_time[0]).local().format("YYYY-MM-DDTHH:mm:ss[Z]")
        : undefined,
      create_time_end:data.create_time && data.create_time[0]
        ?  moment(data.create_time[1]).local().format("YYYY-MM-DDTHH:mm:ss[Z]")
        : undefined,
    };
    this.FnGetList();
  }
  private async FnGetList(){
    let id = ''
    if(this.$route.name === 'waiting_hosts'){
      id = this.$route.params.id.slice(0, this.$route.params.id.length - 1);
    }else {
      id = this.$route.params.id
    }
    let pod=findPodIdByHostId(id)
    const resData: any = await Service.get_gpu_list({
      pod_id:pod,
      page_index:this.page_info.page_index,
      page_size:this.page_info.page_size,
      status:this.search_status.length>0 ? this.search_status.join(',') : this.search_status.map(item=>item.value).join(','),
      host_id:this.$route.params.id,
      ...this.search_reqData
    })
    if (resData.code === "Success") {
      this.gpu_list = resData.data.page_info_list
      this.page_info.total = resData.data.page_info.count
    }
  }
  //handleSizeChange
  private handleSizeChange(val){
    this.page_info.page_size = val;
    this.page_info.page_index = 1
    this.FnGetList()
  }
  private handleCurrentChange(cur){
    this.page_info.page_index = cur
    this.FnGetList();
  }

}
